@use '../abstract' as ab;
@use 'sass:map';

$arrowDir: bottom, top, left, right;

@include ab.build_scheme();
@include ab.build_primary_font();
@include ab.build_spacing();
@include ab.buildGrid();

@each $dir in $arrowDir {
    .arrow-#{$dir}{
        @include ab.arrow($dir);
    }
}
